}

::-webkit-scrollbar-thumb {
    background: black;
} */

*:focus {
  outline: none;
}

h1 {
  font-size: 38px;
  font-family: 'Nunito', sans-serif !important;
  font-weight: 700;
  line-height: 45px;
  color: #424242;
}

h2 {
  font-size: 22px;
  font-family: 'Nunito', sans-serif !important;
  font-weight: 700;
  line-height: 30px;
  color: #272727;
}

h3 {
   font-size: 18px;
  font-family: 'Nunito', sans-serif !important;
  font-weight: 700;
  color: #161A1F;
  margin: 0;
}

h4 {
  font-size: 16px;
  font-family: 'Nunito', sans-serif !important;
  line-height: 20px;
  font-weight: 500;
  margin-top: 0;
  color: #4E5A6E;
}

.subtitle { 
  color: #888888;
}

small {
    font-size: 12px;
}

a, a:link, a:visited {
    color: #555555;
}

a:hover {
    color: #888888;
}

p {
  color: #636363;
}

